.bienvenida-titulo{
    margin-top: 10rem;
    margin-bottom: 10rem;
    max-width: 90rem;
    h1{
        text-align: center;
        color: $blanco;
        text-transform: uppercase;
        font-weight: $bold;
        
        font-size: 3.8rem;

        @include tablet{
            font-size: 6.5rem;
            text-align: left;
        }
    }

    span{
        background: linear-gradient(90deg, $morado, $azulClaro);
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
    }

    p{
        font-size: 2.2rem;
        color: $blanco;

    }
}