html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

body {
  background-color: #090b0c;
}

.header {
  padding: 2rem 0;
}
.header h1 {
  color: #9145B6;
}

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
.logo p {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 3.4rem;
  margin: 0;
}
.logo img {
  width: 7rem;
}

@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion a {
  text-decoration: none;
  color: #FFFFFF;
  font-size: 2.2rem;
  transition: color 0.3s ease;
}
.navegacion a:hover {
  color: #3EDBF0;
}

.bienvenida-titulo {
  margin-top: 10rem;
  margin-bottom: 10rem;
  max-width: 90rem;
}
.bienvenida-titulo h1 {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 3.8rem;
}
@media (min-width: 768px) {
  .bienvenida-titulo h1 {
    font-size: 6.5rem;
    text-align: left;
  }
}
.bienvenida-titulo span {
  background: linear-gradient(90deg, #9145B6, #3EDBF0);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}
.bienvenida-titulo p {
  font-size: 2.2rem;
  color: #FFFFFF;
}

.conocimientos {
  margin-bottom: 5rem;
  margin-top: 10rem;
}
.conocimientos h2 {
  font-weight: 700;
  margin-bottom: 10rem;
}
.conocimientos h2, .conocimientos h3, .conocimientos p {
  color: #FFFFFF;
}
.conocimientos h3, .conocimientos p {
  margin: 0;
}
.conocimientos p {
  margin-top: 2rem;
}
@media (min-width: 1024px) {
  .conocimientos {
    flex-direction: row;
    justify-content: center;
  }
}
.conocimientos .tecnologia {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: hsl(200, 14%, 8%);
  border-radius: 10px;
}
.conocimientos .tecnologia img {
  width: 11rem;
}
.conocimientos a {
  border: 1px solid #3EDBF0;
  border-radius: 5px;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  text-transform: uppercase;
  display: inline-block;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;
}
.conocimientos a:hover {
  background-color: #3EDBF0;
  color: #000000;
  box-shadow: 0 0 10px 2px #3EDBF0;
}
.conocimientos .descripcion {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding: 5rem;
  margin-top: 5rem;
  background-color: hsl(200, 14%, 8%);
  border-radius: 10px;
}
@media (min-width: 1024px) {
  .conocimientos .descripcion {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .conocimientos .descripcion h3 {
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .conocimientos .descripcion-texto {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .conocimientos .descripcion-imagenes {
    grid-column: 3/4;
  }
}

.imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
}
.imagenes img {
  width: 7rem;
}

.tecnologias {
  display: grid;
  row-gap: 5rem;
}
@media (min-width: 1024px) {
  .tecnologias {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 5rem;
  }
}

.info-tecnologia {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.datos {
  margin-bottom: 5rem;
  margin-top: 5rem;
}
.datos h2 {
  color: #FFFFFF;
  font-weight: 700;
}

@media (min-width: 1024px) {
  .datos-contenido {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.datos-texto p {
  color: #FFFFFF;
  font-size: 2.2rem;
}

.redes-sociales {
  margin: 5rem 0 2.5rem 0;
  display: flex;
  justify-content: center;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .redes-sociales {
    justify-content: start;
  }
}
.redes-sociales img {
  width: 5rem;
  border: 1px solid #3EDBF0;
  border-radius: 10px;
}

.boton {
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .boton {
    justify-content: left;
  }
}
.boton a {
  border: 1px solid #3EDBF0;
  border-radius: 5px;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  text-transform: uppercase;
  display: inline-block;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;
}
.boton a:hover {
  background-color: #3EDBF0;
  color: #000000;
  box-shadow: 0 0 10px 2px #3EDBF0;
}

.datos-imagen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 10rem;
}
.datos-imagen img {
  border-radius: 50%;
  width: 30rem;
}
.datos-imagen h3 {
  color: #FFFFFF;
}

.info-entrada {
  display: flex;
  gap: 1rem;
}
.info-entrada img {
  width: 5rem;
}

.datos-contacto h3, .datos-contacto p {
  color: #FFFFFF;
}

.prueba {
  height: 50rem;
  background-color: #090b0c;
  opacity: 0.8;
  background-image: linear-gradient(#00344f 1.8px, transparent 1.8px), linear-gradient(to right, #00344f 1.8px, #090b0c 1.8px);
  background-size: 36px 36px;
}

.footer {
  text-align: center;
}
.footer h3, .footer p, .footer a {
  color: #FFFFFF;
}
.footer h3 {
  margin-top: 5rem;
  font-weight: 700;
}
.footer p, .footer a {
  font-size: 2.2rem;
}
.footer a {
  transition: color 0.3s ease;
}
.footer a:hover {
  color: #3EDBF0;
}
@media (min-width: 768px) {
  .footer {
    text-align: left;
  }
}

.contenido-footer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .contenido-footer {
    flex-direction: row;
    gap: 10rem;
  }
}

.links nav {
  display: flex;
  flex-direction: column;
}

.sociales nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 2rem;
}
.sociales nav img {
  width: 5rem;
}

.contacto p {
  margin: 0;
}

.copyright {
  text-align: center;
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .links, .contacto, .sociales {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
}